.App {
  text-align: center;
  background-color: skyblue;
  position: relative;
  top: 40px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App input{
  padding: 0px;
  height: 35px;
  width: 281px;
  border-radius: 10px;
  border: none;
  outline: none;
}
.App button{
  height: 35px;
  border-radius: 10px;
  border: none;
  width: 69px;
}

.App-link {
  color: #61dafb;
}
.App #weather_details{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
